// Variables
$primary-color: #1a1a1a;
$secondary-color: #ffffff;
$transition-duration: 0.3s;

// Global styles
body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background-color: $primary-color;
  color: $secondary-color;
}

// Navigation
.nav-tabs {
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 2rem 0;
  background: rgba(0, 0, 0, 0.8);
  position: sticky;
  top: 0;
  z-index: 100;

  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .nav-item {
    cursor: pointer;
    padding: 0.5rem 1rem;
    transition: opacity $transition-duration;

    &:hover {
      opacity: 0.8;
    }

    &.active {
      border-bottom: 2px solid $secondary-color;
    }
  }
}

// Gallery
.gallery-container {
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  
  @media (max-width: 768px) {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 1rem;
    padding: 1rem;
  }
}

.gallery-item {
  position: relative;
  cursor: pointer;
  overflow: hidden;
  border-radius: 8px;
  transition: transform $transition-duration;

  &:hover {
    transform: scale(1.05);
  }

  img {
    width: 100%;
    height: auto;
    display: block;
  }
}

// Modal
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0;
  transition: opacity $transition-duration;

  &.active {
    display: flex;
    opacity: 1;
  }

  .modal-content {
    position: relative;
    max-width: 90%;
    max-height: 90vh;

    img {
      max-width: 100%;
      max-height: 90vh;
      object-fit: contain;
    }
  }

  .close-button {
    position: absolute;
    top: 1rem;
    right: 1rem;
    width: 2rem;
    height: 2rem;
    background: $secondary-color;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color $transition-duration;

    &:hover {
      background-color: darken($secondary-color, 10%);
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      width: 1rem;
      height: 2px;
      background-color: $primary-color;
      transform-origin: center;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }
}
